.regions {
    background-color: aqua;
    cursor: pointer;
    padding: 10px 20px;
}

.wrapper {
    width: 1000px;
    height: 650px;
    display: flex;
    font-weight: 300;
    flex-direction: column;
    align-items: center;
    background: white;
    box-shadow: inset 0px 0px 20px 2px rgba(0, 0, 0, 0.1);
    overflow: hidden;
}

.content {
    width: 100%;
    height: 100%;
    padding: 30px 100px;
}

.input {
    border-bottom: black 1px solid;
    font-size: 18px;
    color: black;
    padding: 10px;
    resize: none;
    width: 100%;
}

.input:focus {
    outline: none;
}

.inputWrapper {
    display: flex;
    width: 100%;
    justify-content: left;
}

.formPrice {
    border: 1px solid black;
    border-radius: 10px;
    width: 80%;
    font-size: 20px;
    background: white;
    color: black;
    padding: 10px;
    resize: none;
}

.priceWrap {
    display: flex;
    width: 100%;
    margin-bottom: 30px;
}

.formPriceWrapper {
    position: relative;

}

.formPriceTitle {
    position: absolute;
    z-index: 0;
    left: 30px;
    top: -11px;
    background: white;
    padding: 0px 5px;
}

.steps {
    font-size: 18px;
    font-weight: 300;
}

.progressWrap {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100px;
    background: rgb(232, 232, 232);
    padding: 20px;
    box-shadow: inset 0px 0px 20px 2px rgba(0, 0, 0, 0.1)
}

.progress {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 10px;
    background-color: white;
    width: 100%;
}

.progress__inner {
    height: 100%;
    width: 80%;
    background: #0065cb;
    transition: all 0.3s ease-in-out;
    background: linear-gradient(90deg, #0065cb 0%, #003972 100%);
}

.header {
    text-align: center;
    font-size: 18px;
    margin-bottom: 20px;
}

.headerStart {
    text-align: left;
    font-size: 18px;
    display: flex;
    align-items: center;
    padding-left: 40px;
}

.firstButtonWrapper {
    position: absolute;
    bottom: 0;
    right: 0;
}

.buttonWrapper {
    position: absolute;
    bottom: 0;
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-top: 30px;
}

.disabledButton {
    border: 1px solid rgba(0, 0, 0, 0.35);
    width: max-content;
    color: rgba(0, 0, 0, 0.35);
    padding: 15px 30px;
    border-radius: 5px;
}

.backButton {
    border: 1px solid black;
    width: max-content;
    color: black;
    padding: 15px 30px;
    border-radius: 5px;
    transition: 0.5s ease;
    cursor: pointer;
}

.button {
    border: 1px solid #0065cb;
    width: max-content;
    color: #0065cb;
    padding: 15px 30px;
    border-radius: 5px;
    transition: 0.5s ease;
    cursor: pointer;
}

.button:hover {
    box-shadow: 0px 0px 30px 4px #0065cb54;
    transform: translateY(-5px);
}

.backButton:hover {
    box-shadow: 0px 0px 30px 4px #00000054;
    transform: translateY(-5px);
}

.list {
    list-style: none;
    padding: 0;
}

.element {
    background: black;
    color: gainsboro;
    padding: 15px 30px;
    border-radius: 5px;
    transition: 0.5s ease;
    cursor: pointer;
    margin: 15px 0px;
}

.element:hover {
    background: gainsboro;
    color: black;
    box-shadow: 0px 0px 8px 4px rgba(0, 0, 0, 0.346);
}

.values {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    padding-left: 10px;
    padding-right: 7px;
}

.value {
    font-weight: 300;
}

.image {
    width: 70px;
    height: 70px;
    position: absolute;
    top: -30px;
    left: -10px;
}

.formInput {
    border: 1px solid #0065cb82;
    border-radius: 5px;
    width: 100%;
    font-size: 18px;
    background: white;
    color: black;
    padding: 10px 15px;
    resize: none;
}

.inputBlock {
    margin-top: 20px;
    position: relative;
    width: 100%;
}

.formBlock {
    position: relative;
    margin-top: 20px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.formTitle {
    position: absolute;
    z-index: 0;
    left: 30px;
    top: -10px;
    background: white;
    padding: 0px 5px;
    color: #0065cb;
}

.formArea {
    border: 1px solid #0065cb82;
    border-radius: 5px;
    width: 100%;
    font-size: 20px;
    background: white;
    color: black;
    padding: 10px 15px;
    height: 100px;
    box-sizing: border-box;
    resize: none;
}

.error {
    color: red;
}

.formCheckboxWrapper {
    display: flex;
    font-size: 20px;
    color: #0065cb;
}

.formCheckbox {
    margin-right: 20px;
}